/* STATIC STYLES */

.shine-card3 {
  position: relative;
  border-radius: 2rem;
  background: var(--color-primary);
  overflow: hidden;
  padding: 2rem;
  transition: box-shadow 0.4s ease;
}

.shine-card3-inner {
  position: relative;
  z-index: 2;
}

.shine-card3::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(
    circle at var(--shine-x, 50%) var(--shine-y, 50%),
    rgba(255, 255, 255, var(--shine-opacity, 0.5)),
    transparent 60%
  );
  pointer-events: none;
  transition: background 0.2s ease;
  z-index: 1;
}

.shine-card3.glow:hover {
  box-shadow: 0 0 30px var(--glow-color, rgba(255, 255, 255, 0.3));
}

